* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial;
    background: #002844;
}

.App {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

form {
    min-width: 600px;
    padding: 24px;
    border-radius: 8px;
    background: #fff;
    display: flex;
    flex-direction: column;
    gap: 24px;
}

input {
    width: 100%;
    padding: 12px;
    border: 1px solid #002844;
    outline: none;
    border-radius: 6px;
}

input:focus {
    border: 2px solid #002844;
}

button {
    padding: 12px;
    background: #002844;
    border-radius: 6px;
    color: #fff;
    border: none;
    width: 100%;
    font-size: 18px;
}

button:disabled,
button[disabled] {
    background: rgba(0, 40, 68, 0.92);
    color: rgba(255, 255, 255, 0.25);
}